@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat"), url("../fonts/montserrat-light.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat"), url("../fonts/montserrat-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: local("Montserrat"), url("../fonts/montserrat-medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: local("Montserrat"), url("../fonts/montserrat-semibold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: local("Montserrat"), url("../fonts/montserrat-bold.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #2d383f;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 120px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.header {
  padding-top: 40px;
  color: #ffffff;
  background-color: #6b90b6;
}
@media (max-width: 1199px) {
  .header {
    padding-top: 30px;
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 15px;
  }
}
@media (max-width: 1199px) {
  .header--open {
    padding-bottom: 70px;
    background-color: #ffffff;
  }
  .header--open .header__phone-link {
    color: #2d383f;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1199px) {
  .header__wrapper {
    display: grid;
    grid-template-columns: repeat(3, auto);
    row-gap: 43px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    grid-template-columns: repeat(2, auto);
    row-gap: unset;
  }
}

.header__logo-link svg {
  fill: #ffffff;
}
@media (max-width: 1199px) {
  .header__logo-link svg {
    width: 144px;
    height: 44px;
  }
}
@media (max-width: 767px) {
  .header__logo-link svg {
    width: 112px;
    height: 34px;
  }
}
@media (max-width: 1199px) {
  .header__logo-link {
    margin-left: 101px;
    align-self: center;
  }
}
@media (max-width: 1199px) {
  .header__logo-link--open {
    margin-left: 0;
  }
  .header__logo-link--open svg {
    fill: #2d383f;
  }
}

.header__nav ul {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 31px;
  list-style: none;
}
@media (max-width: 1199px) {
  .header__nav {
    display: none;
    grid-row: 2/3;
  }
  .header__nav--open {
    display: block;
  }
  .header__nav ul {
    flex-direction: column;
    row-gap: 26px;
    color: #2d383f;
    text-transform: uppercase;
    font-weight: 700;
    line-height: 16px;
    letter-spacing: 0.64px;
  }
}

.header__phone-link {
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .header__phone-link {
    font-size: 16px;
    line-height: 16px;
  }
}
@media (max-width: 767px) {
  .header__phone-link {
    grid-column: 2/3;
    justify-self: end;
    font-size: 14px;
    line-height: 14px;
  }
}

.header__burger-btn {
  display: none;
}
@media (max-width: 1199px) {
  .header__burger-btn {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 0;
    width: 26px;
    height: 26px;
    border: none;
    background-color: rgba(255, 255, 255, 0);
    cursor: pointer;
  }
  .header__burger-btn::after, .header__burger-btn::before {
    display: block;
    height: 10%;
    width: 100%;
    background-color: #ffffff;
    content: "";
  }
}
@media (hover: hover) {
  .header__burger-btn:hover:not(.focus-visible) .header__burger-line {
    width: 100%;
  }
}
.header__burger-btn--open {
  position: relative;
}
.header__burger-btn--open::after, .header__burger-btn--open::before {
  position: absolute;
  top: 9px;
  left: 0;
  right: 0;
  width: 22px;
  height: 2px;
  background-color: #2d383f;
}
.header__burger-btn--open::after {
  transform: rotate(45deg);
}
.header__burger-btn--open::before {
  transform: rotate(-45deg);
}
.header__burger-btn--open .header__burger-line {
  display: none;
}

.header__burger-line {
  position: relative;
  display: block;
  width: 66%;
  height: 10%;
  background-color: #ffffff;
  transition: width 0.3s ease;
}

